.main {
  background-image: url("../img/main-triangles.svg");
  background-position: 94% -2%;
  background-size: auto;
  background-repeat: no-repeat;

  @media (max-width: $tablet-width) {
    background-image: url("../img/main-triangle.svg");
    background-position: 282px 14px;
    background-size: auto;
  }

  @media (max-width: $mobile-width) {
    background-position: 11% 22%;
  }
}
